<template>
    <div class="mainBox">
      <div class="logoBox">
        <img src="../../img/logo.png" style="width: 2rem;">
      </div>
      <div class="loginRow">
        <span>用户名</span>
        <input type="text" placeholder="请输入用户名">
      </div>
      <div class="loginRow">
        <span>密码</span>
        <input type="text" placeholder="请输入密码">
      </div>
      <div class="loginBtn">
        登录
      </div>
      <div class="forgetPwd">忘记密码</div>
    </div>
</template>

<script>
  export default {
    name: "index"
  }
</script>

<style scoped>
  .mainBox{
    box-sizing: border-box;
    width: 100%;
    padding: 1rem;
  }
  .logoBox{
    text-align: left;
    margin-bottom: 2rem;
  }
  .loginRow{
    box-sizing: border-box;
    width: 100%;
    border-bottom: 2px solid #e4e5e9;
    padding: .3rem;
    font-size: .4rem;
    text-align: left;
    margin-bottom: .4rem;
  }
  .loginRow span{
    display: inline-block;
    width: 1.2rem;
  }
  .loginRow input{
    margin-left: .4rem;
    outline: none;
  }
  .loginBtn{
    margin-top: 1rem;
    width: 100%;
    text-align: center;
    height: 1.2rem;
    line-height: 1.2rem;
    font-size: .5rem;
    background: #04dfe1;
    color: #fff;
    border-radius: 4px;
  }
  .forgetPwd{
    margin-top: .4rem;
    font-size: .4rem;
    text-align: right;
  }
</style>



