<template>
  <div>
    <div class="header">
      <div class="iconfont iconzuojiantou back" @click="back"/>
      <div v-if="like == false" class="like iconfont iconshoucang1" @click="likeThis"/>
      <div v-else class="like iconfont iconcollection-b" style="color: #00c2c2;" @click="likeThis"/>
    </div>
    <cube-slide ref="slide">
      <cube-slide-item class="bannerItem">
        <img src="../img/aj4-1-1.png" class="bannerImg">
      </cube-slide-item>
      <cube-slide-item class="bannerItem">
        <img src="../img/aj4-1-3.png" class="bannerImg">
      </cube-slide-item>
      <cube-slide-item class="bannerItem">
        <img src="../img/aj4-1-4.png" class="bannerImg">
      </cube-slide-item>
      <cube-slide-item class="bannerItem">
        <img src="../img/aj4-1-5.png" class="bannerImg">
      </cube-slide-item>
      <cube-slide-item class="bannerItem">
        <img src="../img/aj4-1-6.png" class="bannerImg">
      </cube-slide-item>
    </cube-slide>
    <div class="goodsName">Air Jordan 4 "Tattoo"黑白涂鸦 中国元素</div>
    <div class="goodsPrice">￥1399</div>
    <div class="chooseSize">
    	选择尺码
    	<span class="pleaseChoose" @click="showSizeBox">
    		请选择尺码<i class="iconfont iconjiantou" style="font-size: .3rem;"></i>
    	</span>
    </div>
    <div class="interview"/>
		<img src="../img/server.png" class="serverImg"/>
    <div class="interview"/>
    <div class="goodsDetailInfo">
    	<p>
    		配色
    		<span>
    			黑/棕/白
    		</span>
    	</p>
    	<p>
    		货号
    		<span>
    			VN0A3TKMVSO
    		</span>
    	</p>
    </div>
    <div class="interview"/>
    <img src="../img/sizeTable.png" style="width: 100%;margin: 1rem 0;"/>
    <img src="../img/progress.png" style="width: 100%;margin: 1rem 0;"/>
    <div v-if="!showSize" class="buttonGroup">
    	<button class="sell" @click="showSizeBox">出售</button>
    	<button class="buy" @click="showSizeBox">立即购买</button>
    </div>
    <div v-if="showSize" class="sizeBox">
    	<div class="sizeBoxHeader">
    		<div class="iconfont iconcha close" @click="closeSizeBox"/>
    		<img src="../img/aj4-1-1.png"/>
	    	<div class="sizeBoxPriceBox">
	    		<p>￥2449</p>
	    		<p>选择尺码</p>
	    	</div>
    	</div>
    	<div class="allSize">
    		<div :class="{'selected':showIndex}" @click="selectSize">
    				<p style="font-size: .46rem;margin-bottom: .2rem;">36</p>
    				<p style="font-size: .4rem;">￥3016</p>
    		</div>
    		<div>
    			<p style="font-size: .46rem;margin-bottom: .2rem;">37</p>
    			<p style="font-size: .4rem;">￥3016</p>
    		</div>
    		<div>
    			<p style="font-size: .46rem;margin-bottom: .2rem;">38</p>
    			<p style="font-size: .4rem;">￥3016</p>
    		</div>
    		<div>
    			<p style="font-size: .46rem;margin-bottom: .2rem;">39</p>
    			<p style="font-size: .4rem;">￥3016</p>
    		</div>
    		<div>
    			<p style="font-size: .46rem;margin-bottom: .2rem;">40</p>
    			<p style="font-size: .4rem;">￥3016</p>
    		</div>
    		<div>
    			<p style="font-size: .46rem;margin-bottom: .2rem;">41</p>
    			<p style="font-size: .4rem;">￥3016</p>
    		</div>
    		<div>
    			<p style="font-size: .46rem;margin-bottom: .2rem;">42</p>
    			<p style="font-size: .4rem;">￥3016</p>
    		</div>
    		<div>
    			<p style="font-size: .46rem;margin-bottom: .2rem;">42</p>
    			<p style="font-size: .4rem;">￥3016</p>
    		</div>
    		<div>
    			<p style="font-size: .46rem;margin-bottom: .2rem;">43</p>
    			<p style="font-size: .4rem;">￥3016</p>
    		</div>
    		<p v-if="showIndex" class="buyBtn">
    			<button @click="buy">立即购买</button>
    		</p>
    	</div>
    </div>
    <div v-if="showSize" class="blackBg" @click="closeSizeBox"  @touchmove.prevent/>
  </div>
</template>

<script>
export default {
  name: "GoodsDetail",
  data() {
  	return {
  		like: false,
  		showSize: false,
  		showIndex: false
  	}
  },
  methods: {
  	likeThis: function() {
  		this.like = !this.like
  	},
  	showSizeBox: function(){
  		this.showSize = !this.showSize
  	},
  	closeSizeBox: function() {
  		this.showSize = false
  	},
  	selectSize: function() {
  		this.showIndex = !this.showIndex
  	},
    back: function() {
      this.$router.back(-1)
    },
    buy: function() {
      this.$router.push({path: '/OrderDetail'})
    }
  }
}
</script>

<style scoped>
  .header{
    box-sizing: border-box;
    height: .8rem;
    width: 100%;
    padding: .2rem .4rem 0 .4rem;
  }
  .back{
    float: left;
    font-size: .6rem;
  }
  .like{
    float: right;
    font-size: .5rem;
    margin-top: .1rem;
  }
  .liked{
  	color: #00c2c2 !important;
  }
  .bannerItem{
    height: 3.6rem;
  }
  .bannerImg{

    height: 100%;
  }
  .goodsName{
  	box-sizing: border-box;
  	width: 100%;
  	padding: 0 .4rem;
  	font-size: .5rem;
  	margin: .4rem 0;
  	text-align: center;
  }
  .goodsPrice{
  	font-size: .52rem;
  	margin-bottom: .4rem;
  }
  .chooseSize{
  	height: 1rem;
		line-height: 1rem;
		text-align: left;
		padding: 0 .4rem;
  	color: #807f8d;
  	font-size: .2rem;
  	border-top: 1px solid #ececee;
  }
  .pleaseChoose{
  	float: right;
  	color: #000;
  	align-items: center;
  }
  .interview{
  	width: 100%;
  	height: .2rem;
  	background: #f5f5f7;
  }
  .serverImg{
  	width: 100%;
  }
  .goodsDetailInfo{
  	font-size: .24rem;
  	color: #83838b;
  	margin-top: .4rem;
  }
  .goodsDetailInfo p{
  	box-sizing: border-box;
  	height: .8rem;
  	line-height: .6rem;
  	width: 100%;
  	padding: 0 .4rem;
  	text-align: left;
  }
  .goodsDetailInfo p span{
  	float: right;
  }
  .buttonGroup{
  	position: fixed;
  	z-index: 2;
  	box-sizing: border-box;
  	display: flex;
  	flex-direction: row;
  	width: 100%;
  	padding: 0 .4rem;
		position: fixed;
  	bottom: 0;
  	left: 0;
  	background: #fff;
  	padding-top: .4rem;
  	border-top: 1px solid #d2d1d7;
  }
  .buttonGroup button{
  	height: 1rem;
  	text-align: center;
  	line-height: 1rem;
  	outline: none;
  	border-radius: 4px;
  	margin-bottom: .2rem;
  }
  .sell{
  	flex: 1;
  	background: #fff;
  	border: 1px solid #d2d1d7;
  	margin-right: .2rem;
  }
  .buy{
  	flex: 3;
  	background: #00c2c2;
  	color: #fff;
  	border: 1px solid #00c2c2;
  }
  .sizeBox{
  	position: fixed;
  	z-index: 4;
  	bottom: 0;
  	left: 0;
  	box-sizing: border-box;
  	width: 100%;
  	text-align: left;
  	font-size: .4rem;
  	background: #fff;
  }
  .sizeBox img{
  	width: 3rem;
  }
  .sizeBoxPriceBox{
  	display: inline-block;
  }
  .sizeBoxHeader{
  	position: relative;
  	box-sizing: border-box;
  	width: 100%;
  	display: flex;
  	align-items: center;
  	line-height: .7rem;
  	padding: .4rem;
  }
  .close{
  	position: absolute;
  	top: .4rem;
  	right: .4rem;
  	font-size: .5rem;
  }
  .allSize{
  	box-sizing: border-box;
  	background: #f5f4f9;
  	width: 100%;
  	padding: .4rem;
  	display: flex;
  	flex-direction: row;
  	flex-wrap: wrap;
  }
  .allSize div{
  	width: 1.8rem;
  	height: 1.8rem;
  	margin-right: .4rem;
  	margin-bottom: .4rem;
  	background: #fff;
  	display: flex;
  	justify-content: center;
  	align-items: center;
  	flex-direction: column;
  	border-radius: 4px;
  	border: 1px solid #fff;
  }
  .eachSize:nth-child(4n){
  	margin-right: 0;
  }
  .blackBg{
  	position: fixed;
  	z-index: 3;
  	bottom: 0;
  	left: 0;
  	width: 100%;
  	height: 100%;
  	background: #000000;
  	opacity: .4;
  }
  .selected{
  	border: 1px solid #000000 !important;
  }
  .buyBtn{
  	width: 100%;
  	padding: .2rem .4rem;
  }
  .buyBtn button{
  	width: 100%;
  	height: 1rem;
  	background: #00c2c2;
  	color: #fff;
  	border: none;
  	outline: none;
  	border-radius: 4px;
  }
</style>



