<template>
    <div>
      <div class="header">
          <div class="iconfont iconzuojiantou back" @click="back"/>
          我的收藏
      </div>
      <div class="goodsRow" @click="viewGoods">
        <img src="../../img/aj4-1.png" alt="">
        <div>
          <div class="goodsName">Air Jordan 4 "Tattoo"黑白涂鸦 中国元素</div>
          <div style="font-size: .36rem;text-align: left">
            <span class="size">41码</span>
            数量*1
            <span class="goodsPrice">￥1899</span>
          </div>
        </div>
      </div>
      <div class="interval"/>
    </div>
</template>

<script>
  export default {
    name: "index",
    methods: {
      back: function() {
        this.$router.push({path: '/PageMy',query: {id: '我'}})
      },
      viewGoods: function() {
        this.$router.push({path: '/GoodsDetail'})
      }
    }
  };
</script>

<style scoped>
  .goodsRow{
    display: flex;
    flex-direction: row;
    box-sizing: border-box;
    width: 100%;
    padding: .4rem;
  }
  .goodsRow img{
    width: 2rem;
    height: 2rem;
    margin-right: .4rem;
  }
  .goodsName{
    font-size: .4rem;
    text-align: left;
    margin: .3rem 0;
  }
  .size{
    margin-right: .3rem;
  }
  .goodsPrice{
    float: right;
  }
  .interval{
    width: 100%;
    height: .2rem;
    background: #f5f5f7;
  }
  .header{
    box-sizing: border-box;
    line-height: 1rem;
    width: 100%;
    padding: .2rem .4rem;
    font-size: .5rem;
    text-align: left;
    border-bottom: 2px solid #e4e5e9;
  }
  .back{
    float: left;
    font-size: .6rem;
    margin-right: .4rem;
  }
</style>



