          <div style="font-size: .36rem;text-align: left">
            <span class="size">41码</span>
            数量*1
            <span class="goodsPrice">￥1899</span>
          </div>
        </div>
      </div>
      <div class="interview"/>
      <img src="../img/orderInfo.png" style="width: 100%;">
      <div class="bottom">
        实付款:<span style="font-size: .56rem;">￥1899</span>
        <div class="button">
          提交订单
        </div>
      </div>
    </div>
</template>

<script>
export default {
  name: "OrderDetail",
  methods: {
    back: function() {
      this.$router.back(-1)
    }
  }
}
</script>

<style scoped>
  .header{
    box-sizing: border-box;
    line-height: 1rem;
    width: 100%;
    padding: .2rem .4rem;
    font-size: .5rem;
    text-align: left;
    border-bottom: 2px solid #e4e5e9;
  }
  .back{
    font-size: .6rem;
    margin-right: .4rem;
  }
  .userInfo{
    text-align: left;
    padding: .4rem;
    font-size: .4rem;
  }
  .address{
    margin-top: .3rem;
    font-size: .36rem;
    color: #d4d4d4;
  }
  .interview{
    width: 100%;
    height: .2rem;
    background: #f5f5f7;
  }
  .goodsRow{
    display: flex;
    flex-direction: row;
    box-sizing: border-box;
    width: 100%;
    padding: .4rem;
  }
  .goodsRow img{
    width: 2rem;
    height: 2rem;
    margin-right: .4rem;
  }
  .goodsName{
    font-size: .4rem;
    text-align: left;
    margin: .3rem 0;
  }
  .size{
    margin-right: .3rem;
  }
  .goodsPrice{
    float: right;
  }
  .bottom{
    position: fixed;
    bottom: 0;
    height: 1.6rem;
    line-height: 1rem;
    box-sizing: border-box;
    width: 100%;
    padding: .36rem;
    font-size: .4rem;
    color: #F56C6C;
    text-align: left;
  }
  .button{
    float: right;
    width: 3rem;
    height: 1rem;
    line-height: 1rem;
    background: #04dfe1;
    text-align: center;
    font-size: .4rem;
    border-radius: 4px;
    color: #fff;
  }
</style>



