<template>
  <div>
    <div class="header">
      <span @click="cancle">取消</span>
      <span style="float: right;color: #04dfe1;">发布</span>
    </div>
    <div class="container">
      <cube-input v-model="value" placeholder="说点什么吧..."></cube-input>
      <cube-upload
        ref="upload"
        v-model="files"
        :action="action"
        :max="1"
        @files-added="addedHandler"
        @file-error="errHandler"
        class="upLoad">
        <div class="clear-fix">
          <cube-upload-file v-if="files.length > 0" :file="files[0]"></cube-upload-file>
          <cube-upload-btn
            v-if="files.length == 0"
            :multiple="false"/>
        </div>
      </cube-upload>
    </div>
  </div>
</template>

<script>
  export default {
    name: "index",
    data() {
      return {
        value: '',
        action: '//jsonplaceholder.typicode.com/photos/',
        files: []
      }
    },
    methods: {
      addedHandler() {
        const file = this.files[0]
        file && this.$refs.upload.removeFile(file)
      },
      errHandler(file) {
        // const msg = file.response.message
        this.$createToast({
          type: 'warn',
          txt: 'Upload fail',
          time: 1000
        }).show()
      },
      cancle: function(){
        this.$router.push({path: '/find',query: {id: '发现'}})
      }
    }
  }
</script>

<style scoped>
  .header{
    box-sizing: border-box;
    line-height: 1rem;
    width: 100%;
    padding: .2rem .4rem;
    font-size: .5rem;
    text-align: left;
    border-bottom: 2px solid #e4e5e9;
    color: #e7e6eb;
  }
  .container{
    box-sizing: border-box;
    width: 100%;
    padding: .4rem;
  }
  .upLoad{
    width: 2.1rem;
    height: 2.14rem;
    margin-top: 1rem;
    color: #e7e6eb;
  }
  .upLoad{
    border: 1px solid #e7e6eb;
  }
</style>



