<template>
  <div>
    <div class="container">
      <div class="header">
        <cube-input v-model="value" class="searchInput iconfont iconsousuo" placeholder="输入商品名称、货号"></cube-input>
        <router-link to="/AllType">
          <img src="../../img/allType.jpg" class="typeImg">
        </router-link>
      </div>
    </div>
    <cube-slide ref="slide">
      <cube-slide-item class="bannerItem">
        <img src="../../img/banner1.png" class="bannerImg">
      </cube-slide-item>
      <cube-slide-item class="bannerItem">
        <img src="../../img/banner2.png" class="bannerImg">
      </cube-slide-item>
      <cube-slide-item class="bannerItem">
        <img src="../../img/banner3.png" class="bannerImg">
      </cube-slide-item>
    </cube-slide>
    <img src="../../img/info.png" class="infoImg">
    <goods-list/>
  </div>
</template>

<script>
import GoodsList from '@/views/GoodsList'
export default {
  name: "index",
  components: {
    GoodsList
  },
  data() {
    return {
      value: ''
    }
  }
}
</script>

<style scoped>
  .container{
    margin: 0 .2rem;
  }
  .header{
    margin-top: .5rem;
    display: flex;
    flex-direction: row;
    width: 100%;
    margin-bottom: .5rem;
  }
  .searchInput{
    box-sizing: border-box;
    height: 1rem;
    width: 85%;
    margin-right: 4%;
    background: #f5f4f9;
    font-size: .4rem;
    padding-left: .2rem;
  }
  .typeImg{
    height: 1rem;
  }
  .bannerItem{
    height: 3.6rem;
  }
  .bannerImg{
    width: 100%;
    height: 100%;
  }
  .infoImg{
    margin-top: .4rem;
    width: 100%;
  }
</style>



