<template>
  <div>
    <div style="height: .8rem;text-align: left;padding: .4rem 0 0 .4rem;border-bottom: 1px solid #f5f4f9;">
      <div class="iconfont iconzuojiantou back" style="font-size: .5rem;" @click="back"/>
    </div>
    <cube-tab-bar v-model="selectedLabel">
      <cube-tab label="待发货" class="navFz">
        <p>待发货</p>
        <p>0</p>
      </cube-tab>
      <cube-tab label="已发货" class="navFz">
        <p>已发货</p>
        <p>1</p>
      </cube-tab>
      <cube-tab label="交易成功" class="navFz">
        <p>交易成功</p>
        <p>2</p>
      </cube-tab>
      <cube-tab label="交易失败" class="navFz">
        <p>交易失败</p>
        <p>0</p>
      </cube-tab>
    </cube-tab-bar>
    <div class="interview"/>
    <cube-tab-panels v-model="selectedLabel">
      <cube-tab-panel label="待发货">
      </cube-tab-panel>
      <cube-tab-panel label="已发货">
        <div class="goodsRow">
          <img src="../img/aj4-1.png" alt="">
          <div>
            <div class="goodsName">Air Jordan 4 "Tattoo"黑白涂鸦 中国元素</div>
            <div style="font-size: .36rem;text-align: left">
              <span class="size">41码</span>
              数量*1
              <span class="goodsPrice">￥1899</span>
            </div>
          </div>
        </div>
        <div class="interview"/>
      </cube-tab-panel>
      <cube-tab-panel label="交易成功">
      </cube-tab-panel>
      <cube-tab-panel label="交易失败">
      </cube-tab-panel>
    </cube-tab-panels>
  </div>
</template>

<script>
export default {
  name: "HistoryList",
  data() {
    return {
      selectedLabel: '待发货'
    }
  },
  methods: {
    back: function() {
      this.$router.push({path: '/PageMy',query: {id: '我'}})
    },
  }
}
</script>

<style scoped>
  .navFz{
    font-size: .46rem;
  }
  .navFz p{
    margin-top: .2rem;
  }
  .interview{
    width: 100%;
    height: .2rem;
    background: #f5f5f7;
  }
  .goodsRow{
    display: flex;
    flex-direction: row;
    box-sizing: border-box;
    width: 100%;
    padding: .4rem;
  }
  .goodsRow img{
    width: 2rem;
    height: 2rem;
    margin-right: .4rem;
  }
  .goodsName{
    font-size: .4rem;
    text-align: left;
    margin: .3rem 0;
  }
  .size{
    margin-right: .3rem;
  }
  .goodsPrice{
    float: right;
  }
</style>



